<template>
  <section class="carousel">
    <div class="carousel--main">
      <div class="carousel--bg">
        <img :src="posts[currentPost].thumb" alt="" />
      </div>
      <div class="carousel--desc">
        <h1 class="title">{{ posts[currentPost].title || '' }}</h1>
        <p class="desc">{{ posts[currentPost].title || '' }}</p>
        <BaseButton iconclass="fal fa-chevron-right" color="#e94e77" background="white" style="border: 1px dashed #e94e77;">Read More</BaseButton>
      </div>
    </div>
    <div class="carousel--aside">
      <div class="carousel--item" v-for="post in posts" :key="post.id">
        <div class="wrapper" @mouseenter="selectThis(post)">
          <time :datetime="post.date" style="color:#7a7a7a;">{{ getDate(post) }}</time>
          <a class="title" :href="post.href">{{ post.title }}</a>
          <a class="desc" :href="post.cateHref">{{ post.cate }}</a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    posts: {
      type: Array,
      default: function() {
        return [
          {
            title: '',
            cate: '',
            href: '',
            cateHref: ''
          }
        ]
      }
    }
  },
  data() {
    return {
      currentPost: 0
    }
  },
  methods: {
    getDate(post) {
      const date = post.date || new Date()
      return `${date.getFullYear()}-${date.getMonth()}-${date.getDate()}`
    },
    selectThis(post) {
      this.currentPost = post.id
    }
  }
}
</script>

<style lang="scss" scoped>
$text-underline-color: #e94e77;
$dash-border-color: #e94e77;
$text-hover-color: #e94e77;
@mixin shadow {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3), 1px 1px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
@mixin round {
}
.carousel {
  margin: auto;
  width: 100%;
  height: 600px;
  display: flex;

  .carousel--main {
    border-radius: 10px;
    @include shadow();

    flex: 3.5 1;
    position: relative;
    h1,
    p {
      margin: 1em 0;
    }
    h1 {
      font-size: 30px;
      font-weight: bolder;
      font-style: italic;
      text-decoration: #e94e77 underline;
    }
    p {
      text-transform: uppercase;
    }
    .carousel--bg {
      border-radius: 10px;
      overflow: hidden;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      img {
        display: block;
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    .carousel--desc {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      padding: 1em 3.5em;

      // top: 50%;
    }
  }
  .carousel--aside {
    @include shadow;
    flex: 1 1;
    padding-top: 1em;
    flex-direction: column;
    box-sizing: border-box;
    margin-left: 50px;
    display: flex;
    time {
      font-size: 12px;
    }
    a {
      cursor: pointer;
      display: block;
      &.title {
        font-size: 14px;
        margin: 0.4em 0;
        color: #242424;
        text-decoration: none;
        &:hover {
          color: $text-hover-color;
          text-decoration: $text-underline-color underline;
        }
      }
      &.desc {
        font-size: 12px;
        font-weight: bold;
        color: #7a7a7a;
        &:hover {
          color: $text-hover-color;
          text-decoration: $text-underline-color underline;
        }
      }
    }

    .carousel--item {
      flex: 1;
      display: flex;
      overflow: hidden;

      .wrapper {
        flex: 1 1;
        overflow: hidden;
        border-radius: 0px 0px 10px 10px;
        margin: 0 1em 1em;
        padding: 0em 0.5em;
        transition: 0.3s ease-in;
        &:hover {
          cursor: pointer;
          box-shadow: 0px 0px 5px $text-hover-color;
        }
      }
      &:not(:first-child) .wrapper {
        border-top: 1px dashed $dash-border-color;
      }
      &:first-child .wrapper {
        // border-top: 1px dashed $dash-border-color;
      }
    }
  }
}
</style>
